/**
 * è§¦å‘å™¨
 * - ç›‘å¬ MESSAGE_RECEIVED äº‹ä»¶
 * - åŒæ­¥é˜¶æ®µï¼šåˆ›å»ºå ä½æ¡ç›® â†’ åŒæ­¥ enabled â†’ æ›´æ–°æ¥¼å±‚å¯è§æ€§
 * - å¼‚æ­¥é˜¶æ®µï¼šå°†ä»»åŠ¡åŠ å…¥é˜Ÿåˆ—
 */

import { getSettings } from '@/config';
import { taskQueue } from '@/queue';
import { createPlaceholderMiniSummary, syncMiniSummaryEnabled, worldbookExists } from '@/worldbook';
import { updateFloorVisibility } from '@/chat-manager';

/** å°æ€»ç»“è®¡æ•°å™¨ï¼ˆç”¨äºå¤§æ€»ç»“æ£€æŸ¥é—´éš”ï¼‰ */
let miniSummaryCount = 0;

/** åç½®æ€»ç»“ï¼šä¸Šä¸€æ¡å¾…æ€»ç»“çš„æ¶ˆæ¯ ID */
let pendingMessageId: number | null = null;

/** æ£€æŸ¥æ˜¯å¦åº”è¯¥è§¦å‘å¤§æ€»ç»“æ£€æŸ¥ */
function shouldCheckVolumeSummary(): boolean {
  const settings = getSettings();
  if (!settings.auto_volume_summary) return false;
  return miniSummaryCount % settings.check_interval === 0 && miniSummaryCount > 0;
}

/** MESSAGE_RECEIVED äº‹ä»¶å¤„ç†å‡½æ•° */
async function onMessageReceived(message_id: number): Promise<void> {
  const settings = getSettings();

  // å¦‚æœæœªå¯ç”¨è‡ªåŠ¨å°æ€»ç»“ï¼Œç›´æ¥è¿”å›
  if (!settings.auto_mini_summary) return;

  // è·³è¿‡ç³»ç»Ÿæ¶ˆæ¯
  const messages = getChatMessages(message_id);
  if (messages.length === 0) return;
  const msg = messages[0];
  if (msg.role === 'system') return;
  if (!msg.message || msg.message.trim() === '') return;

  // å¿½ç•¥å‰ N å±‚çš„æ¶ˆæ¯
  if (message_id < settings.ignore_floors) return;

  // ä¸–ç•Œä¹¦ä¸å­˜åœ¨åˆ™è·³è¿‡
  if (!worldbookExists()) return;

  try {
    // === åç½®æ€»ç»“é€»è¾‘ ===
    if (settings.deferred_summary) {
      // åç½®æ¨¡å¼ï¼šå…ˆå¤„ç†ä¸Šä¸€æ¡å¾…æ€»ç»“çš„æ¶ˆæ¯
      if (pendingMessageId !== null) {
        await createPlaceholderMiniSummary(pendingMessageId);
        miniSummaryCount++;
        if (shouldCheckVolumeSummary()) {
          taskQueue.enqueue({ type: 'volume_summary' });
        }
        taskQueue.enqueue({ type: 'mini_summary', message_id: pendingMessageId });
      }
      // è®°ä½å½“å‰æ¶ˆæ¯ä¸ºå¾…å¤„ç†
      pendingMessageId = message_id;
    } else {
      // === å³æ—¶æ¨¡å¼ï¼ˆåŸæœ‰é€»è¾‘ï¼‰ ===

      // 1. æ–°å»ºå½“å‰æ¥¼å±‚çš„å°æ€»ç»“ä¸–ç•Œä¹¦æ¡ç›®ï¼ˆå†…å®¹ä¸ºç©ºå ä½ï¼‰
      await createPlaceholderMiniSummary(message_id);

      // 2. é€’å¢è®¡æ•°å™¨
      miniSummaryCount++;

      // 3. å¤§æ€»ç»“æ£€æŸ¥ï¼ˆå¦‚æœæ»¡è¶³é—´éš”æ¡ä»¶ï¼Œå°†å¤§æ€»ç»“ä»»åŠ¡åŠ å…¥é˜Ÿåˆ—ï¼‰
      if (shouldCheckVolumeSummary()) {
        taskQueue.enqueue({ type: 'volume_summary' });
      }

      // 4. å°†å°æ€»ç»“ç”Ÿæˆä»»åŠ¡åŠ å…¥å¼‚æ­¥é˜Ÿåˆ—
      taskQueue.enqueue({ type: 'mini_summary', message_id });
    }

    // === åŒæ­¥é˜¶æ®µï¼ˆä¸¤ç§æ¨¡å¼å…±ç”¨ï¼‰ ===

    // åŒæ­¥å°æ€»ç»“ enabled çŠ¶æ€
    await syncMiniSummaryEnabled();

    // è°ƒæ•´æ¥¼å±‚éšè—ä¸æ˜¾ç¤º
    await updateFloorVisibility();
  } catch (e) {
    console.error('[è‡ªåŠ¨æ€»ç»“] æ¶ˆæ¯å¤„ç†å¤±è´¥:', e);
  }
}

/** æ³¨å†Œäº‹ä»¶ç›‘å¬ */
export function registerListeners(): EventOnReturn {
  return eventOn(
    tavern_events.MESSAGE_RECEIVED,
    errorCatched((message_id: number) => {
      void onMessageReceived(message_id);
    })
  );
}
